import React from 'react';
import styled, { css } from 'styled-components';
import { COLORS, CSS_BORDER_BOX, CSS_CURSOR_DISABLE, CSS_DISABLE_SELECT } from '../../common';
import { TEXT_CSS } from '../../basic/text';
import { ISelectProps } from './Select';

const CSS_BORDERED_ENABLED = css`
  &:hover {
    border-color: ${COLORS.OTHERS.PRIMARY_HOVER};
  }
  &:active {
    border-color: ${COLORS.COMMON.BLUE1_ACTIVE_COLOR};
  }
  &:focus-within {
    border-color: ${COLORS.DING_BLUR};
  }
`;

const CSS_BORDERED_DISABLED = css`
  border-color: ${COLORS.COMMON.OVERLAY_COLOR};
`;

const CSS_BORDERED_ERRORED = css`
  border-color: ${COLORS.COMMON.DANGER1_COLOR};
`;

const CSS_VISIBLE = css`
  border-color: ${COLORS.DING_BLUR};
  box-shadow: 0px 0px 0px 2px rgba(0, 106, 254, 0.12);
`;

export const visibleStyle: React.CSSProperties = { borderColor: COLORS.DING_BLUR, boxShadow: '0px 0px 0px 2px rgba(0, 106, 254, 0.12)' };

const HOVER_TRANSITION_BG = css`
  &:hover {
    background: ${COLORS.OTHERS.DAO_HOVER_BG_COLOR};
  }
  &:active {
    background: ${COLORS.OTHERS.BG_ACTIVE};
  }
  transition: background-color 0.1s ease-in-out;
`;

const CSS_VISIBLE_BG = css`
  background: ${COLORS.OTHERS.BG_FOCUS};
  transition: background-color 0.1s ease-in-out;
`;

type IOutInputWrapProps = Required<Pick<ISelectProps, 'size'>>;

export const InputWrap = styled.div<IOutInputWrapProps>`
  position: relative;
  border-radius: 4px;
  height: ${({ size }) => (size === 'normal' ? '32px' : '28px')};
`;

type IInputWrapProps = Required<Pick<ISelectProps, 'size' | 'disabled' | 'bordered' | 'visible'>> & { hasError: boolean };

export const SelectNormal = styled.div<IInputWrapProps>`
  ${CSS_BORDER_BOX}
  ${TEXT_CSS.TEXT}
  ${CSS_CURSOR_DISABLE};

  display: flex;
  align-items: center;

  width: 100%;
  height: ${({ size }) => (size === 'normal' ? '32px' : '28px')};

  padding: 4px 4px 4px 8px;

  border-radius: 4px;
  ${CSS_DISABLE_SELECT};
  outline: none;

  color: ${({ disabled }) => disabled ? COLORS.LIGHT.LEVEL4_BASE_COLOR : COLORS.LIGHT.LEVEL1_BASE_COLOR};
  background-color: ${({ disabled, bordered, visible }) => (
    bordered ? (disabled ? '#F7F6F6' : 'unset') :
      visible ? COLORS.OTHERS.BG_FOCUS : 'unset'
  )};
  border: ${({ bordered }) => bordered ? `1px solid ${COLORS.COMMON.OVERLAY_COLOR}` : '1px solid transparent'};
  ${({ disabled, bordered, hasError, visible }) => (
    !bordered ? (disabled ? null :
      visible ? CSS_VISIBLE_BG :
      HOVER_TRANSITION_BG) :
      disabled ? CSS_BORDERED_DISABLED :
        hasError ? CSS_BORDERED_ERRORED : CSS_BORDERED_ENABLED)
  };
  ${({ visible, bordered }) => (visible && bordered ? CSS_VISIBLE : null)};

  &:focus-within {
    ${({ disabled, bordered }) => (!disabled && bordered ? 'box-shadow: 0px 0px 0px 2px rgba(0, 106, 254, 0.12)' : null)}
  }
`;

export const SelectNormalValue = styled.div`
  flex-grow: 1;
  min-width: 10px;
`;

export const SelectNormalValuePlaceholder = styled.span`
  margin: 0 4px;
  color: ${COLORS.LIGHT.LEVEL4_BASE_COLOR};
`;

export const SuffixWrap = styled.span`
  display: inline-block;

  margin-left: 4px;
  margin-right: 4px;
  
  line-height: 0;

  cursor: default;
`;

export const errorStyle: React.CSSProperties = { borderColor: COLORS.COMMON.DANGER1_COLOR };

type IErrorProps = Pick<ISelectProps, 'size'>;

export const ErrorTip = styled.div<IErrorProps>`
  position: absolute;
  top: ${({ size }) => (size === 'normal' ? '32px' : '28px')};
  left: 0;

  margin-top: 4px;

  font-size: 12px;
  line-height: 18px;
  
  color: ${COLORS.COMMON.DANGER1_COLOR};
`;
